import { Component, ViewChild }                                      from '@angular/core';
import { IonicPage, NavController,   Platform, ModalController, Slides }  from 'ionic-angular';
import { SocialSharing }                                  from '@ionic-native/social-sharing';
import { Developments }                                   from '../../providers';
import { DomSanitizer }                                   from '@angular/platform-browser';
import { PopoverController } from 'ionic-angular';
import { GalleryModal } from 'ionic-gallery-modal';

 

declare var google: any;

@IonicPage()
@Component({
  selector: 'page-development-detail',
  templateUrl: 'development-detail.html',
})

export class DevelopmentDetailPage {
  public map        : any; 
  public imageIcon  : any ; 
  public mapstyle   : any;
  public mapProp    : any ;
  public mapView    = true;
  public flag        : Boolean = false;
  @ViewChild('slides') slides: Slides;
  view: { namenities: number, showamenities: boolean, btnshowamenities: boolean } = { namenities: 10, showamenities: false, btnshowamenities : true}
  
  item : any    = [];
  btnDocuments  = false;
  btnShare      = false;
  
  constructor(public modalCtrl: ModalController,public popoverCtrl: PopoverController, public platform: Platform, public sanitized: DomSanitizer, public socialSharing: SocialSharing, public development: Developments, private navCtrl: NavController) {
  } 

  ionViewDidLoad() {
    this.item = this.development.development;

    if (this.item.video != 0)
      this.item.video = this.sanitized.bypassSecurityTrustResourceUrl(this.item.video);

      this.item.m2cFrom = Math.round(this.item.m2cFrom);
    this.item.m2cTo = Math.round(this.item.m2cTo);
  
  }
  
  showDocuments (folio){

    if(this.btnDocuments)
      return;
    this.btnDocuments = true;

    var promise = this.development.getDocuments(folio);
    
    Promise.all([promise]).then(data => {
      this.navCtrl.push('DocumentsPage');
      this.btnDocuments = false;
    });


  }

  contact(){
    this.navCtrl.push('ContactPage', 1);
  }

  share(){

    if(this.btnShare)
      return;

    this.btnShare = true;

    this.socialSharing.share(this.item.developmentNameEs, '', '', this.item.flyerEs+ '&source=app').then(() => {

      this.btnShare = false;
    }).catch(() => {
      this.btnShare = false;

    });

  }

  loadMap() {


    if (this.item.latitude == 0 || this.item.latitude == '128.000000'){

      this.mapView = false;

    }else{

      this.mapView = true;

      this.mapstyle = [{
        featureType: "poi.business",
        elementType: "labels",
        
      }];

      let LatLng = new google.maps.LatLng(this.item.latitude, this.item.longitude);
      this.mapProp = {
        streetViewControl: false,
        zoomControl: false,
        mapTypeControl: false,
        scaleControl: false,
        setMyLocationEnabled: false,
        fullscreenControl : false,
        setCompassEnabled: false,
        styles: this.mapstyle,
        zoom: 14,
        center: LatLng
      };

      var mapEle: HTMLElement = document.getElementById('mapa');
      this.map = new google.maps.Map(mapEle, this.mapProp);
      new google.maps.Marker({ position: LatLng, map: this.map, title: '' });   
      
    }
  }

  gomap(){
    this.navCtrl.push('MapPage', this.item);
  }

  prevSlide(){
    this.slides.slidePrev();
  }

  nextSlide(){
    this.slides.slideNext(); 
  }

  moreInfo(){ 

    this.navCtrl.push('MoreInfoPage', {
      descriptionEs: this.item.descriptionEs,
      interiores: this.item.interiores,
      exterior: this.item.exterior,
      services: this.item.services,
      amenidades: 0,
      servicios: 0
    });

  }

  ionViewWillEnter() {  
    //para no cargar el mapa dos veces :,v 
    if(!this.flag){


      if (this.platform.is('android')){
        this.loadMap();
      }else{
        this.mapView = false;
      }
      this.flag = true;
    }
  }

  openGallery(){
    let modal = this.modalCtrl.create(GalleryModal, {
      photos: this.item.images,
      initialSlide: 0
    });
    modal.present();
  }  

}


